<template>
  <div class="main">
    <span class="headerTitle"></span>
    <el-card class="box-card">
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="first">
          <div>
            <el-input size="large" placeholder="" v-model="username" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-user"></i>
                  <span></span>
                </div>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder=""
              v-model="password"
              show-password
              class="input"
            >
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-lock"></i>
                  <span></span>
                </div>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder=""
              v-model="verifyCode"
              class="input"
              @keyup.enter.native="Login"
            >
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>

            <div class="btnGround">
              <el-button type="success" @click="Login" :disabled="loginBtnDisplay"></el-button>
              <el-button type="primary" @click="dialogFormVisible = true"></el-button>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div>
            <el-input size="large" placeholder="" v-model="photo" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-phone-outline"></i>
                  <span></span>
                </div>
              </template>
            </el-input>
            <el-input size="large" placeholder="" v-model="verifyCode3" class="input">
              <template slot="prepend">
                <div class="inputHeader">
                  <i class="el-icon-lock"></i>
                  <span></span>
                </div>
              </template>
              <template slot="suffix">
                <span class="btnCode" @click="getCode(photo)"></span>
              </template>
            </el-input>
            <el-input
              size="large"
              placeholder=""
              v-model="verifyCode2"
              class="input"
              @keyup.enter.native="Login2"
            >
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
            <div class="btnGround">
              <el-button type="success" @click="Login2" :disabled="loginBtnDisplay"></el-button>
              <el-button type="primary" @click="dialogFormVisible = true"></el-button>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <div class="bottom">
        <el-link type="primary" class="bottomBtn" @click="dialogPasswordVisible = true"></el-link>
        <el-link type="primary" class="bottomBtn" @click="dialogInfoVisibel = true"></el-link>
      </div>
    </el-card>
    <el-dialog title="" :visible.sync="dialogFormVisible" :close-on-click-modal="false" destroy-on-close>
      <el-form :model="form" label-position="top" size="small" ref="registerForm" :rules="rules">
        <el-form-item label="" prop="userName">
          <el-input v-model="form.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userIdentity">
          <el-input v-model="form.userIdentity" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userSecurityQuestion">
          <el-select v-model="form.userSecurityQuestion" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userSecurityAnswer">
          <el-input v-model="form.userSecurityAnswer" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userPassword">
          <el-input v-model="form.userPassword" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input v-model="form.confirmPassword" show-password autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userPhone">
          <div class="formPhoto">
            <el-input v-model="form.userPhone" autocomplete="off"></el-input>
            <el-button type="primary" size="small" @click="getCode(form.userPhone)"></el-button>
          </div>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="form.code" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="Register" size="medium"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogInfoVisibel"
      :close-on-click-modal="false"
      class="dialogInfo"
    >
      <div style="padding:28px;background:#f5f5f5">
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        >http://www.csls.cdb.com.cn 111520211520 (****.cdb@sina.cn) POS  15</div>
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        >5 </div>
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        >  800012000 http://www.csls.cdb.com.cn </div>
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        >11(****.cdb@sina.cn) </div>
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        > http://www.csls.cdb.com.cn(****.cdb@sina.cn)24 95188 95188</div>
        <div class="questionTitle">//</div>
        <div
          class="questionContent"
        > 1015 </div>
        <div class="questionTitle"></div>
        <div
          class="questionContent"
        > http://www.csls.cdb.com.cn </div>
        <div slot="footer" class="button">
          <el-button type="primary" @click="dialogInfoVisibel = false" size="medium"> </el-button>
        </div>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="dialogIdentity?'':''"
      :visible.sync="dialogPasswordVisible"
      :close-on-click-modal="false"
      @closed="dialogIdentity=false"
    >
      <div class="dialogPassword" v-if="!dialogIdentity">
        <div class="passwordCard">
          <div class="passwordText">
            <div class="passwordTitle"></div>
            <div class="passwordContent"></div>
          </div>

          <el-button type="primary" size="small" @click="dialogIdentity = true;dialogType = 1"></el-button>
          <div></div>
        </div>
        <div class="passwordCard">
          <div class="passwordText">
            <div class="passwordTitle"></div>
          </div>

          <el-button type="primary" size="small" @click="dialogIdentity = true;dialogType = 2"></el-button>
          <div></div>
        </div>
      </div>
      <div v-else style="padding:10px 0 30px 0">
        <el-form
          label-position="top"
          size="small"
          style="width:300px;margin-bottom:30px"
          ref="identityForm"
          :model="dialogFrom"
          :rules="rule"
        >
          <el-form-item label="" prop="identity">
            <el-input v-model="dialogFrom.identity" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="code">
            <el-input size="large" placeholder="" v-model="dialogFrom.code">
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer" style="margin-top:10px">
          <el-button @click="dialogIdentity = false"> </el-button>
          <el-button type="primary" @click="checkPassword">  </el-button>
        </span>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialog1" :close-on-click-modal="false" destroy-on-close>
      <div></div>
      <div>{{dialog1Form.userPhone}}</div>
      <el-form
        :model="dialog1Form"
        label-width="145px"
        label-position="top"
        style="width:400px"
        ref="updateForm1"
        :rules="rules1"
      >
        <el-form-item label="" prop="code">
          <div style="display:flex;align-items: center;">
            <el-input size="large" placeholder="" v-model="dialog1Form.code" class="input">
              <template slot="append">
                <div class="ValidCode disabled-select" @click="refreshCode">
                  <span
                    v-for="(item, index) in codeList"
                    :key="index"
                    :style="getStyle(item)"
                  >{{item.code}}</span>
                </div>
              </template>
            </el-input>
            <el-button
              type="primary"
              size="small"
              style="height:40px;margin-left:10px"
              @click="getCode(dialog1Form.userPhone)"
            ></el-button>
          </div>
        </el-form-item>
        <el-form-item label="" prop="verifyCode">
          <el-input size="small" v-model="dialog1Form.verifyCode"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPwd">
          <el-input size="small" v-model="dialog1Form.newPwd" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmNewPwd">
          <el-input size="small" v-model="dialog1Form.confirmNewPwd" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPasswordVisible = true;dialogIdentity = true;dialog1=false;"> </el-button>
        <el-button type="primary" @click="updatePassword"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialog2" :close-on-click-modal="false" destroy-on-close>
      <el-form
        :model="dialog2Form"
        label-position="top"
        size="small"
        ref="updateForm2"
        :rules="rules2"
      >
        <el-form-item label="">
          <el-input v-model="dialog2Form.userSecurityQuestion" autocomplete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="securityAnswer">
          <el-input v-model="dialog2Form.securityAnswer" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPwd">
          <el-input size="small" v-model="dialog2Form.newPwd" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmNewPwd">
          <el-input size="small" v-model="dialog2Form.confirmNewPwd" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPasswordVisible = true;dialogIdentity = true;dialog1=false;"> </el-button>
        <el-button type="primary" @click="updatePassword2"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: "first",
      username: "",
      password: "",
      photo: "",

      dialogFrom: {
        identity: "",
        code: ""
      },

      dialogType: 0,
      verifyCode: "",
      verifyCode2: "",
      verifyCode3: "",

      codeList: [],
      confirmcode: "",
      dialogFormVisible: false,
      dialogInfoVisibel: false,
      dialogPasswordVisible: false,
      dialog1: false,
      dialog2: false,
      dialog1Form: {
        userPhone: "",
        newPwd: "",
        code: "",
        verifyCode: "",
        confirmNewPwd: ""
      },
      dialog2Form: {
        userSecurityQuestion: "",
        securityAnswer: "",
        newPwd: "",
        confirmNewPwd: ""
      },
      formPassword: "",
      loginBtnDisplay: false,
      dialogIdentity: false,
      form: {
        userName: "",
        userIdentity: "",
        userSecurityQuestion: "",
        userSecurityAnswer: "",
        userPassword: "",
        confirmPassword: "",
        userPhone: ""
      },
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "",
            trigger: "blur"
          },
          {
            max: 15,
            message: '"  "15',
            trigger: "blur"
          }
        ],
        userIdentity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],
        userSecurityQuestion: [
          { required: true, message: "", trigger: "change" }
        ],
        userSecurityAnswer: [
          { required: true, message: "", trigger: "blur" }
        ],
        userPassword: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          }
        ],
        confirmPassword: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.form.userPassword !== value) {
                return callback(new Error(""));
              } else {
                return callback();
              }
            },
            trigger: "blur"
          }
        ],
        userPhone: [
          { required: true, message: "", trigger: "blur" }
        ],
        code: [{ required: true, message: "", trigger: "blur" }]
      },
      rule: {
        identity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],
        code: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error(""));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      },
      rules1: {
        newPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          }
        ],
        confirmNewPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.dialog1Form.newPwd !== value) {
                return callback(new Error(""));
              }
              return callback();
            },
            trigger: "blur"
          }
        ],
        code: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: (rules, value, callback) => {
              if (this.confirmcode.toUpperCase() !== value.toUpperCase()) {
                return callback(new Error(""));
              }
              return callback();
            },
            trigger: "blur"
          }
        ],
        verifyCode: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      rules2: {
        securityAnswer: [
          { required: true, message: "", trigger: "blur" }
        ],
        newPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          }
        ],
        confirmNewPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: "620",
            trigger: "blur"
          },
          {
            validator: (rules, value, callback) => {
              if (this.dialog2Form.newPwd !== value) {
                return callback(new Error(""));
              }
              return callback();
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.createdCode();
  },
  methods: {
    //
    confirmPas() {
      return false;
    },
    refreshCode() {
      this.createdCode();
    },
    createdCode() {
      let len = 4,
        codeList = [],
        chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz0123456789",
        charsLen = chars.length;
      // 
      for (let i = 0; i < len; i++) {
        let rgb = [
          Math.round(Math.random() * 220),
          Math.round(Math.random() * 240),
          Math.round(Math.random() * 200)
        ];
        codeList.push({
          code: chars.charAt(Math.floor(Math.random() * charsLen)), // 
          color: `rgb(${rgb})`, // 
          fontSize: 16 + Number(Math.floor(Math.random() * 15)) + "px", // 
          padding: `${[Math.floor(Math.random() * 10)]}px`, // 
          transform: `rotate(${Math.floor(Math.random() * 90) -
            Math.floor(Math.random() * 90)}deg)` // 
        });
      }
      // 
      this.codeList = codeList;
      this.confirmcode = codeList.map(item => item.code).join("");
    },
    // 
    getStyle(data) {
      return `color: ${data.color}; font-size: ${data.fontSize}; padding: ${data.padding}; transform: ${data.transform}`;
    },
    //
    Login() {
      this.loginBtnDisplay = true;
      if (
        !this.username ||
        !this.password ||
        this.verifyCode.toUpperCase() != this.confirmcode.toUpperCase()
      ) {
        this.$message({
          type: "error",
          message: "",
          center: true,
          onClose: () => {
            this.loginBtnDisplay = false;
          }
        });
        return;
      }
      this.$axios
        .post(
          "/userLogin/byAccount",
          {
            userLoginAccount: this.username,
            userLoginPassword: this.password
          },
          {
            ignore: true
          }
        )
        .then(({ status, data }) => {
          if (status == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            sessionStorage.setItem("token", data.token);
            this.$router.push({ name: "home" });
          }
          if (status == 401) {
            this.$message({
              message: ""
            });
          }
          if (status == 1000) {
            this.$message({
              message: "",
              type: "error"
            });
          }
          this.refreshCode();
          this.verifyCode = '';
          this.loginBtnDisplay = false;
        })
        .catch(error => {
          this.loginBtnDisplay = false;
          console.log(error);
        });
    },
    //
    Login2() {
      this.loginBtnDisplay = true;
      if (
        !this.photo ||
        !this.verifyCode3 ||
        this.verifyCode2.toUpperCase() != this.confirmcode.toUpperCase()
      ) {
        this.$message({
          type: "error",
          message: "",
          center: true,
          onClose: () => {
            this.loginBtnDisplay = false;
          }
        });
        return;
      }
      this.$axios
        .post(
          "/userLogin/loginCheck",
          {
            userPhone: this.photo,
            verifyCode: this.verifyCode3
          },
          {
            ignore: true
          }
        )
        .then(({ status, data }) => {
          if (status == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            sessionStorage.setItem("token", data.token);
            this.$router.push({ name: "home" });
          }
          if (status == 401) {
            this.$message({
              message: ",",
              type: "error"
            });
          }
          if (status == 402) {
            this.$message({
              message: "",
              type: "error"
            });
          }
          this.refreshCode();
          this.verifyCode = '';
          this.loginBtnDisplay = false;
        })
        .catch(error => {
          this.loginBtnDisplay = false;
          console.log(error);
        });
    },
    //
    Register() {
      const { form } = this;
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/user/register",
              {
                ...form
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
                 this.dialogFormVisible = false;
              } else if(status == 401){
                this.$message({
                  message: "",
                  type: "error"
                });
                  this.refreshCode();
                  this.verifyCode = '';
              }else if(status == 500){
                 this.$message({
                  message: "",
                  type: "warning"
                });
                 this.dialogFormVisible = false;
              }
              
             
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    //
    getCode(userPhone) {
      if (!userPhone) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      this.$axios
        .post(
          "/getCode",
          {
            userPhone
          },
          {
            ignore: true
          }
        )
        .then(({ status }) => {
          if (status == 200) {
            this.$message({
              message: "",
              type: "success"
            });
          } else {
            this.$message({
              message: "",
              type: "error"
            });
          }
          this.refreshCode();
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    checkPassword() {
      this.$refs.identityForm.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/user/verifyUser",
              {
                userIdentity: this.dialogFrom.identity
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                const dialog = "dialog" + this.dialogType;
                this[dialog] = true;
                this.dialogPasswordVisible = false;
                if (this.dialogType == 1) {
                  this.$axios
                    .post(
                      "/user/selectPhoneByIdentity",
                      {
                        userIdentity: this.dialogFrom.identity
                      },
                      {
                        ignore: true
                      }
                    )
                    .then(({ status, data }) => {
                      if (status == 200) {
                        this.dialog1Form.userPhone = data.userPhone;
                      } else {
                        this.$message({
                          message: "",
                          type: "error"
                        });
                      }
                    });
                } else {
                  this.$axios
                    .post(
                      "/user/selectSecurityByIdentity",
                      {
                        userIdentity: this.dialogFrom.identity
                      },
                      {
                        ignore: true
                      }
                    )
                    .then(({ status, data }) => {
                      if (status == 200) {
                        this.dialog2Form.userSecurityQuestion =
                          data.userSecurityQuestion;
                      } else {
                        this.$message({
                          message: "",
                          type: "error"
                        });
                      }
                    });
                }
              } else {
                this.$message({
                  message: "",
                  type: "error"
                });
              }
                this.refreshCode();
                this.verifyCode = '';
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    //
    updatePassword() {
      this.$refs.updateForm1.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/userLogin/resetPwdByUserPhone",
              {
                userIdentity: this.dialogFrom.identity,
                verifyCode: this.dialog1Form.verifyCode,
                newPwd: this.dialog1Form.newPwd
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.dialog1 = false;
              } else if (status == 401) {
                this.$message({
                  message: "",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              } else if (status == 402) {
                this.$message({
                  message: "",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              } else if (status == 500) {
                this.$message({
                  message: "",
                  type: "error"
                });
                   this.refreshCode();
                  this.verifyCode = '';
              }
            });
        } else {
          return false;
        }
      });
    },
    //2
    updatePassword2() {
      this.$refs.updateForm2.validate(valid => {
        if (valid) {
          this.$axios
            .post(
              "/userLogin/resetPwdByUserSecurity",
              {
                userIdentity: this.dialogFrom.identity,
                securityAnswer: this.dialog2Form.securityAnswer,
                newPwd: this.dialog2Form.newPwd
              },
              {
                ignore: true
              }
            )
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.dialog2 = false;
              } else if (status == 401) {
                this.$message({
                  message: ",",
                  type: "error"
                });
              } else if (status == 500) {
                this.$message({
                  message: "",
                  type: "error"
                });
              }
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
